<template>
  <div class="table" ref="ledger">
    <div class="table__head">
      <div class="table__row">
        <div class="table__cell table__cell--head">
          &nbsp;
        </div>

        <div class="table__cell table__cell--head">
          Category
        </div>

        <div class="
            table__cell
            table__cell--head
            table__cell--numeric
          "
        >
          Amount
        </div>
      </div>
    </div>

    <div class="table__body">
      <db-item-row
        v-for="(item, i) in items"
        :editClickHandler="editClickHandler"
        :deleteClickHandler="deleteClickHandler"
        :key="i"
        :item="item"
        :i="i"
      />
    </div>

    <div class="table__foot">
      <div class="table__row">
        <div class="table__cell">
          <db-calculations
            show="remaining"
            :items="items"
            :amountPaid="amountPaid"
          />
        </div>

        <div class="table__cell">
          <db-calculations
            show="spent"
            :items="items"
            :amountPaid="amountPaid"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import dbItemRow from './ItemRow';
  import dbCalculations from "../utils/Calculations";

  export default {
    props: {
      items: {
        required: true,
        type: Array,
      },

      amountPaid: {
        required: true,
        type: [Number, String],
      },

      editClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      deleteClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },
    },

    watch: {
      items (current, previous) {
        if (current.length > previous.length) {
          this.highlightInsertedRow();
        }
      },
    },

    components: {
      dbItemRow,
      dbCalculations,
    },

    methods: {
      highlightInsertedRow () {
        var row = this.$refs.ledger
          .querySelector(".table__body .table__row:first-child");

        row.classList.add(
          "table__row--inserted",
          "table__row--highlighted"
        );

        setTimeout(function() {
          row.classList.remove("table__row--highlighted");

          setTimeout(function() {
            row.classList.remove("table__row--inserted");
          }, 1510);
        });
      },
    },
  }
</script>

<style lang="scss">
  .table {
    max-height: 100vh;
  }

  .table__head {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 0.2rem 0.5rem rgba(0, 0, 0, 0.2);
  }

  .table__body {
    max-height: calc(100vh - 72px);
    overflow-y: auto;
    overflow-x: hidden;

    .table__row {
      &:nth-child(even) {
        background: rgba(0, 0, 0, 0.025);
      }

      &:hover {
        background: rgba(0, 0, 0, 0.09);
      }
    }
  }

  .table__foot {
    background: rgba(0, 0, 0, 0.1);
    box-shadow: 0 -0.2rem 0.5rem rgba(0, 0, 0, 0.2);
    text-align: right;
  }

  .table__row {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    border-bottom: 1px solid rgba(0, 0, 0, 0.125);
    transition: background-color 0.2s;

    &.table__row--inserted {
      transition: background-color 1.5s;
    }

    &.table__row--highlighted {
      background-color: LightSteelBlue;
      transition: none;
    }
  }

  .table__cell {
    padding: 0.5rem 1rem;
    flex: 0 1 33.3333333333%;

    &.table__cell--head {
      font-weight: bold;
    }

    &.table__cell--numeric {
      text-align: right;
    }

    &.table__cell--colspan-2 {
      width: 100%;
    }
  }
</style>



