<template>
  <div class="welcome welcome__expense">
    <h1>Welcome to Dailybread!</h1>

    <p>Please enter your first expense!</p>

    <db-item-form
      :save="save"
      :submitHandler="submitHandler"
      @saveFailed="save = false"
    />

    <button @click="save = true">Save</button>
  </div>
</template>

<script>
  import dbItemForm from '../ledger/ItemForm';

  export default {
    data() {
      return {
        save: false,
      };
    },

    props: {
      submitHandler: {
        required: true,
      },
    },

    components: {
      dbItemForm,
    },
  };
</script>

<style lang="scss" scoped>
  button {
    float: right;
    padding: 0.5rem 1.5rem;
    font-size: 1.2rem;
    background: MediumSeaGreen;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 0.2rem;
    font-weight: bold;
    text-shadow: 0.05rem 0.05rem 0 rgba(0, 0, 0, 0.5);
    box-shadow: 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.2);
    transition: background-color 0.2s;
    outline: 0;

    &::selection {
      background: none;
    }

    &:hover {
      background: darken(#3cb371, 15%);
    }

    &:active {
      transform: translate(2px, 2px);
    }
  }
</style>



