<template>
  <div class="table__row">
    <div class="table__cell table__row-actions">
      <fa-icon
        name="times"
        class="action action--remove"
        @click.native="deleteClickHandler(i)"
      />

      <fa-icon
        name="edit"
        class="action action--edit"
        @click.native="editClickHandler(i)"
      />
    </div>

    <div class="table__cell">
      {{ item.category }}
    </div>

    <div class="table__cell table__cell--numeric">
      <db-currency :value="item.amount" />
    </div>
  </div>
</template>

<script>
  import 'vue-awesome/icons';
  import faIcon from 'vue-awesome/components/Icon';
  import dbCurrency from '../utils/Currency.vue';

  export default {
    name: "ItemRow",
    props: {
      editClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      deleteClickHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      item: {
        required: true,
        type: Object,
      },

      i: {
        required: true,
        type: Number,
      },
    },

    components: {
      faIcon,
      dbCurrency,
    },
  };
</script>

<style lang="scss">
  .table__row:hover {
    .action {
      opacity: 1;
    }
  }

  .table__row-actions {
    text-align: right;
  }

  .action {
    opacity: 0;
    padding: 0 0.5rem;
    margin: 0;
    cursor: pointer;
    transition: color 0.2s, opacity 0.2s;
  }

  .action::selection {
    background: none;
  }

  .action--edit {
    font-size: 0.9rem;
    font-weight: normal;
    color: CadetBlue;

    &:hover {
      color: Teal;
    }
  }

  .action--remove {
    color: red;

    &:hover {
      color: darkred;
    }
  }
</style>



