<template>
  <fieldset>
    <legend>Add an expense</legend>

    <label>
      Category:
      <input
        type="text"
        class="field"
        ref="category"
        v-model="item.category"
        @keyup.enter="handleSubmit"
      />
    </label>

    <label>
      Amount
      <money
        class="field"
        ref="amount"
        @keyup.native.enter="handleSubmit"
        v-model="item.amount"
        v-bind="money"
      />
    </label>
  </fieldset>
</template>

<script>
  import { Money } from 'v-money';

  var LedgerItem = function(amount, category) {
    this.amount = amount || 0;
    this.category = category || "";
    this.madeOn = new Date();
  };

  export default {
    name: "ItemForm",

    data () {
      return {
        item: new LedgerItem(),

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    props: {
      save: {
        required: false,
      },

      submitHandler: {
        required: false,
        type: Function,
        default: () => { return false; },
      },

      editingItem: {
        required: false,
        type: Object,
        default: () => {},
      },
    },

    components: {
      Money,
    },

    watch: {
      save () {
        if (this.save && !this.handleSubmit()) {
          this.$emit("saveFailed");
        }
      },

      editingItem (item) {
        this.item = new LedgerItem(item.amount, item.category);
      },
    },

    methods: {
      handleSubmit () {
        var valid = validateFields(this);

        if (valid) {
          this.submitHandler(this.item);
          this.item = new LedgerItem();
          this.$refs.category.focus();
          return true;
        } else {
          return false;
        }

        function validateFields(vm) {
          var amount = parseFloat(vm.item.amount).toFixed(2),
              category = vm.item.category;

          if (window.errorTimeout) {
            clearTimeout(window.errorTimeout);
            window.errorTimeout = null;
          }

          if (["0.00", 0, NaN].includes(amount)) {
            vm.$refs.amount.$el.classList.add("field--error");

            window.errorTimeout = setTimeout(function() {
              vm.$refs.amount.$el.classList.remove("field--error");
            });

            vm.$refs.amount.$el.focus();
            vm.$refs.amount.$el.select();

            return false;
          } else if (!category.length) {
            vm.$refs.category.classList.add("field--error");

            window.errorTimeout = setTimeout(function() {
              vm.$refs.category.classList.remove("field--error");
            });

            vm.$refs.category.focus();

            return false;
          } else {
            return true;
          }
        }
      },
    },

    mounted () {
      this.$refs.category.focus();
    },
  };
</script>

<style lang="scss">
  .field {
    transition: background-color 1.5s;
  }

  .field--error {
    background-color: Pink;
    transition: none;
  }
</style>



