<template>
  <div id="db" class="grid">
    <template v-if="amountPaid == 0 || amountPaid == '0.00'">
      <db-income-first-timer
        :initialIncome="amountPaid"
        :initialPayDay1="formattedPayDay1"
        :initialPayDay2="formattedPayDay2"
        :submitHandler="handleIncomeSet"
      />
    </template>

    <template v-else-if="!ledgerItems.length">
      <db-expense-first-timer :submitHandler="recordItem" />
    </template>

    <template v-else>
      <div class="grid__col--9">
        <db-ledger
          :items="ledgerItems"
          :amountPaid="amountPaid"
          :editClickHandler="editItem"
          :deleteClickHandler="deleteItem"
        />
      </div>

      <div class="grid__col--3">
        <h1>{{ currentMonthName }}</h1>

        <db-item-form
          :submitHandler="recordItem"
          :editingItem="editingItem"
        />

        <fieldset>
          <legend>Income settings</legend>

          <label>
            Income each payday
            <money v-model="amountPaid" v-bind="money"></money>
          </label>

          <label>
            First pay day
            <input
              type="date"
              v-model="formattedPayDay1"
            />
          </label>

          <label>
            Second pay day
            <input
              type="date"
              v-model="formattedPayDay2"
            />
          </label>
        </fieldset>

        <p>
          <strong>Next pay day:</strong>
          {{ daysUntilNextPayDay }}
        </p>

        <p>
          <db-calculations
            show="perDay"
            :items="ledgerItems"
            :daysRemaining="getDaysRemaining()"
            :amountPaid="amountPaid"
          />
        </p>
      </div>
    </template>
  </div>
</template>

<script>
  import moment from 'moment';
  import { Money } from 'v-money';

  import Storage from './utils/Storage';
  import dbCalculations from "./utils/Calculations";

  import dbIncomeFirstTimer from './welcome/IncomeFirstTimer';
  import dbExpenseFirstTimer from './welcome/ExpenseFirstTimer';

  import dbItemForm from './ledger/ItemForm';
  import dbLedger from './ledger/Ledger';

  export default {
    data () {
      return {
        amountPaid: Storage.get("amountPaid", {
          parser: "FLOAT",
          default: 0,
        }),

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },

        currentMonthName: moment().format("MMMM"),

        editingItem: null,
        editingRow: null,

        ledgerItems: Storage.get("ledgerItems", {
          parser: "JSON",
          default: "[]",
        }),

        payDay1: moment("2018-01-12"),
        payDay2: moment("2018-01-31"),
      };
    },

    components: {
      dbIncomeFirstTimer,
      dbExpenseFirstTimer,
      Money,
      dbItemForm,
      dbLedger,
      dbCalculations,
    },

    watch: {
      ledgerItems () {
        Storage.set(
          "ledgerItems",
          this.ledgerItems,
          { formatter: "JSON" }
        );
      },

      amountPaid (value) {
        Storage.set("amountPaid", value);
      },

      payDay1 (value) {
        Storage.set(this.currentMonthName + ".payDay1", value);
      },

      payDay2 (value) {
        Storage.set(this.currentMonthName + ".payDay2", value);
      },
    },

    computed: {
      formattedPayDay1: {
        get: function() {
          return this.payDay1.format("YYYY-MM-DD");
        },

        set: function(value) {
          this.payDay1 = moment(value);
        },
      },

      formattedPayDay2: {
        get: function() {
          return this.payDay2.format("YYYY-MM-DD");
        },

        set: function(value) {
          this.payDay2 = moment(value);
        },
      },

      daysUntilNextPayDay: function() {
        if (moment().startOf("day") < this.payDay1.startOf("day")) {
          return this.payDay1.startOf("day")
            .from(moment().startOf("day"));
        } else {
          return this.payDay2.startOf("day")
            .from(moment().startOf("day"));
        }
      },
    },

    methods: {
      recordItem: function(item) {
        var items = this.ledgerItems.slice(0);

        if (this.editingRow != null) {
          items[this.editingRow] = item;
        } else {
          items.unshift(item);
        }

        this.editingRow = null;
        this.ledgerItems = items;
      },

      deleteItem: function(i) {
        this.ledgerItems.splice(i, 1);
      },

      editItem: function(i) {
        this.editingRow = i;
        this.editingItem = this.ledgerItems[this.editingRow];
      },

      handleIncomeSet (income, payDay1, payDay2) {
        this.amountPaid = income;
        this.payDay1 = moment(payDay1);
        this.payDay2 = moment(payDay2);
      },

      getDaysRemaining: function() {
        if (moment() < this.payDay1) {
          return this.payDay1.startOf("day")
            .diff(moment().startOf("day"), "days");
        } else {
          return this.payDay2.startOf("day")
            .diff(moment().startOf("day"), "days");
        }
      },
    },
  }
</script>

<style lang="scss">
  html {
    box-sizing: border-box;
  }

  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }

  .fa-icon {
    box-sizing: content-box;
  }

  body,
  html,
  #db {
    margin: 0;
    padding: 0;
    height: 100vh;
  }

  #db {
    position: relative;
    font-family: Helvetica Neue, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  .welcome {
    position: absolute;
    top: 50%;
    left: 50%;
    max-width: 45%;
    transform: translate(-50%, -50%);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  p {
    padding: 0;
  }

  a {
    color: #42b983;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0 0 1rem;
    padding: 0;
  }

  fieldset {
    border: 0;
    margin: 0 0 1rem;
    padding: 1rem 0 0;

    legend {
      padding: 0;
      font-size: 0.9rem;
      font-style: italic;
    }
  }

  label,
  input {
    display: block;
    width: 100%;
  }

  input {
    font-size: 1.25rem;
    padding: 0.5rem;
  }

  label {
    margin: 0 0 1rem;
  }

  .grid {
    display: flex;
    flex-direction: column;
    width: 100%;
    height: 100%;
  }

  .grid__col--3 {
    order: 1;
    width: 100%;
    padding: 0 1rem;
  }

  .grid__col--9 {
    order: 2;
    width: 100%;
  }

  @media (min-width: 625px) {
    .grid {
      flex-flow: row nowrap;
    }

    [class*="grid__col--"] {
      height: 100%;
    }

    .grid__col--3 {
      order: 2;
      width: 25%;
      border-left: 1px solid rgba(0, 0, 0, 0.125);
    }

    .grid__col--9 {
      order: 1;
      width: 75%;
    }

    [class*="grid__col--"]:last-child,
    [class*="grid__col--"]:only-child {
      margin-right: 0;
    }
  }
</style>



