<template>
  <div class="welcome welcome__income">
    <h1>Welcome to Dailybread!</h1>

    <p>
      Let's start by entering your pay days for this month, and
      how much you will receive at each pay day.
    </p>

    <label>
      How much you will take home each pay day:
      <money
        ref="income"
        @keyup.native.enter="handleSubmit"
        v-model="income"
        v-bind="money"
      />
    </label>

    <label>
      Your first pay day this month:
      <input type="date" v-model="payDay1" />
    </label>

    <label>
      Your second pay day this month:
      <input type="date" v-model="payDay2" />
    </label>

    <button @click="handleSubmit">Next</button>
  </div>
</template>

<script>
  import { Money } from 'v-money';

  export default {
    data() {
      return {
        income: this.initialIncome,
        payDay1: this.initialPayDay1,
        payDay2: this.initialPayDay2,

        money: {
          decimal: '.',
          thousands: ',',
          prefix: '$',
          suffix: '',
          precision: 2,
          masked: false,
        },
      };
    },

    props: [
      'submitHandler',
      'initialIncome',
      'initialPayDay1',
      'initialPayDay2',
    ],

    methods: {
      handleSubmit () {
        this.submitHandler(
          this.income,
          this.payDay1,
          this.payDay2
        );
      },
    },

    components: {
      Money,
    },

    mounted () {
      this.$refs.income.$el.focus();
    },
  };
</script>

<style lang="scss" scoped>
  button {
    float: right;
    padding: 0.5rem 1.5rem;
    font-size: 1.2rem;
    background: MediumSeaGreen;
    color: white;
    cursor: pointer;
    border: none;
    border-radius: 0.2rem;
    font-weight: bold;
    text-shadow: 0.05rem 0.05rem 0 rgba(0, 0, 0, 0.5);
    box-shadow: 0.1rem 0.1rem 0.05rem rgba(0, 0, 0, 0.2);
    transition: background-color 0.2s;
    outline: 0;

    &::selection {
      background: none;
    }

    &:hover {
      background: darken(#3cb371, 15%);
    }

    &:active {
      transform: translate(2px, 2px);
    }
  }
</style>



